import React, {PureComponent} from 'react';
import PropTypes from 'prop-types';
import {
    Avatar,
    Button,
    PanelHeader,
    PanelHeaderBack, PromoBanner, ScreenSpinner,
    SimpleCell, Snackbar,
} from "@vkontakte/vkui";
import {
    convertMsToNormalTime,
    decOfNum,
    getUrlParams,
    isPlatformDesktop, isPlatformIOS
} from "../../js/utils";
import {getVKUsers, vk_local_users} from "../../js/drawerapp/utils";
import bridge from "@vkontakte/vk-bridge";
import {Icon28ClockOutline, Icon28FilmStripOutline} from "@vkontakte/icons";

const isDesktop = isPlatformDesktop();
let ticker;

export class Shop extends PureComponent {

    constructor(props) {
        super(props);

        this.state = {
            items: []
        }
    }

    componentDidMount() {
        const {socket} = this.props.t;
        ticker = setInterval(() => {
            this.forceUpdate();
        }, 1000);

        socket.call('shop.getList', {}, async r => {
            this.setState({items: r.response});
        });
    }

    componentWillUnmount() {
        clearInterval(ticker);
    }

    render() {
        const
            {t, watchedShopItems, boughtShopItems} = this.props,
            {banners} = t.state,
            {items} = this.state,
            {socket} = t
        ;

        return (
            <React.Fragment>
                <PanelHeader
                    left={<PanelHeaderBack onClick={t.back}/>}
                    separator={false}
                >
                    {
                        isDesktop && 'Магазин'
                    }
                </PanelHeader>
                <div className='Shop'>
                    {
                        !isDesktop && <h2 style={{textAlign: 'left'}}>Магазин</h2>
                    }
                    <p style={{margin: '8px 40px 0 0'}}>
                        Здесь могут появляться акционные предметы, которые можно приобрести за монеты.
                    </p>
                    <div className='ShopItems' style={{paddingBottom: banners[3] && 140}}>
                        {
                            items.length > 0 ? items.map(({id, startTime, count, title, price, img, needAdvert, singleBuy}, index) => {
                                const {hours, minutes, seconds} = convertMsToNormalTime(startTime - Date.now()).str;
                                return <div key={`Shop_${index}`} onClick={() => {
                                    if (Date.now() < startTime || (watchedShopItems.indexOf(id) === -1 && needAdvert))
                                        return;

                                    if (singleBuy && boughtShopItems.indexOf(id) > -1) {
                                        t.setState({
                                            snackbar: <Snackbar onClose={() => t.setState({snackbar: null})}>
                                                Вы уже купили этот товар
                                            </Snackbar>
                                        });
                                    } else {
                                        t.setPopout(<ScreenSpinner/>);

                                        socket.call('shop.buy', {product_id: id}, async r => {
                                            if (r.response) {
                                                if (singleBuy)
                                                    t.setState({boughtShopItems: [...boughtShopItems, id]});
                                                
                                                t.setState({
                                                    snackbar: <Snackbar onClose={() => t.setState({snackbar: null})}>
                                                        Вы успешно купили товар
                                                    </Snackbar>
                                                });
                                                await t.updateData();
                                                t.forceUpdate();
                                            } else {
                                                t.setState({
                                                    snackbar: <Snackbar onClose={() => t.setState({snackbar: null})}>
                                                        {r.message}
                                                    </Snackbar>
                                                });
                                            }
                                            t.setPopout(null);
                                        });
                                    }
                                }}>
                                    {
                                        Date.now() < startTime ?
                                            <div className='TimeCounter'>
                                                <Icon28ClockOutline/>
                                                <span>{hours}:{minutes}:{seconds}</span>
                                            </div>
                                            : (watchedShopItems.indexOf(id) === -1 && needAdvert) &&
                                            <div className='WatchAdvert' onClick={async () => {
                                                t.setPopout(<ScreenSpinner/>);
                                                try {
                                                    await bridge.send('VKWebAppShowNativeAds', {ad_format: 'reward'});
                                                } catch (e) {
                                                }
                                                t.setPopout(null);
                                                t.setState({watchedShopItems: [...watchedShopItems, id]});
                                            }}>
                                                <Icon28FilmStripOutline/>
                                                <span>Разблокировать содержимое</span>
                                            </div>

                                    }
                                    <img className='icon' alt='icon' src={img}/>
                                    <div className='titles'>
                                        <span>{title}</span><br/>
                                        <span>{decOfNum(price, ['монета', 'монеты', 'монет'])}</span>
                                    </div>
                                </div>
                            }) :
                                <div key='Shop_-1' onClick={async () => {
                                    try{
                                        await bridge.send('VKWebAppAllowNotifications');
                                        socket.call('users.enableNotifications');
                                    }catch (e) {
                                    }

                                }}>
                                    <div className='titles'>
                                        <span>Тут пусто :(</span><br/>
                                        <span>Включите уведомления о новых товарах. Мы Вас оповестим, когда появится что-то интересное.</span>
                                    </div>
                                </div>
                        }
                    </div>
                    {
                        banners[3] &&
                        <PromoBanner
                            bannerData={banners[3]}
                            onClose={async () => {
                                banners[3] = false;
                                await t.setState({banners});
                                this.forceUpdate();
                            }}
                        />
                    }
                </div>
            </React.Fragment>
        )
    }
}

Shop.defaultProps = {};

Shop.propTypes = {
    t: PropTypes.object,
    watchedShopItems: PropTypes.object,
    boughtShopItems: PropTypes.object
};

export default Shop;